import UIGoodsInfo from '@/components/UIGoodsInfo.vue'
import UiMoney from '@/components/UiMoney.vue'

export default {
	components: { UIGoodsInfo, UiMoney },
	props : {
		orderInfo : {
			type : Object,
			default : () => ({})
		}
	},
	computed:{
		products (){
			return this.orderInfo.products || []
		}
	}
}
